import axios from "axios";
import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { PRODUCT } from "../api/constants";
import FileUpload from "../components/fileUpload/FileUpload";

const AdminPage = () => {
  const [getImgurl, setGetImgurl] = useState();

  const onSubmit = async (data) => {
    await new Promise((r) => setTimeout(r, 1000));
    console.log(JSON.stringify(data));
    if(getImgurl == null) {
      alert('이미지를 ')
    }
    axios
      .post(`${PRODUCT}`, {
        modelName: data["modelName"],
        brand: data["brand"],
        price: data["price"],
        size: data["size"],
        img: getImgurl
      })
      .then(function (response) {
        // console.log("dta", response.data);
      })
      .catch(function (error) {
        // 오류발생시 실행
      })
      .then(function () {
        // 항상 실행
      });
  };

  const getImgUrl = (url) => {
    setGetImgurl(url);
    console.log("rr", url);
  };

  const {
    register,
    handleSubmit,
    formState: { isSubmitting, isDirty, errors },
  } = useForm();
  return (
    <div className="page">
      <h3>어드민페이지</h3>
      <div>
        <form onSubmit={handleSubmit(onSubmit)}>
          <label htmlFor="brand">브랜드</label>
          <input
            id="brand"
            type="text"
            placeholder="brand"
            aria-invalid={
              !isDirty ? undefined : errors.brand ? "true" : "false"
            }
            {...register("brand", {
              required: "브랜드명 필수 입력입니다.",
            })}
          />

          <label htmlFor="modelName">모델명</label>
          <input
            id="modelName"
            type="text"
            placeholder="modelName"
            aria-invalid={
              !isDirty ? undefined : errors.modelName ? "true" : "false"
            }
            {...register("modelName", {
              required: "이름 필수 입력입니다.",
            })}
          />

          <label htmlFor="price">가격</label>
          <input
            id="price"
            type="number"
            placeholder="price"
            aria-invalid={
              !isDirty ? undefined : errors.price ? "true" : "false"
            }
            {...register("price", {
              required: "가격 필수입력",
            })}
          />

          <label htmlFor="size">사이즈</label>
          <input
            id="size"
            type="string"
            placeholder="size"
            aria-invalid={!isDirty ? undefined : errors.size ? "true" : "false"}
            {...register("size", {
              required: "사이즈 필수입력",
            })}
          />

          <FileUpload getImgUrl={getImgUrl} />
          <label htmlFor="type">타입</label>
          <button type="submit" disabled={isSubmitting}>
            저장
          </button>
        </form>
        {/*
          
       */}

        {/*
       <FileUpload2 />
       */}
      </div>
    </div>
  );
};

export default AdminPage;
